<template>
  <div class="detail-containe">
     <strong class="fw-bold fs-1"> {{ data.name }}</strong>
    <div v-for="(room ,index) in data.roomName" :key="index" class="room mb-3">
      <div class="row">
        <div class="col-5">
          <div class="image-box">
            <img class="image-thumbnail" :src="data.thumbnailImage" alt="" />
          </div>
        </div>
        <div class="col-7">
          <div class="mb-5">
            <strong class="fs-5"> {{ room }} </strong>
          </div>
          <div class="mb-3 border-bottom d-flex justify-content-between">
            <p class="fw-bold">가격</p>
            <p class="fw-bold fs-5">{{ formatNumber(data.price[index]) }}원</p>
          </div>
          <div class=" d-flex justify-content-between">
            <button id="btn-detail">
              <p class="text-start">객실 이용안내</p>
              
            </button>
          </div>
          <div>
            <button id="btn-detail-2">
              예약
              
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  props: ["id", "number"],
  data() {
    return {
      data: [],
    };
  },
  methods:{
    formatNumber(number) {
      // 숫자를 문자열로 변환
      const numberString = number.toString();

      // 숫자를 ','를 기준으로 분할
      const parts = numberString.split(".");

      // 정수 부분에 ',' 추가
      parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",");

      // 다시 합치기
      return parts.join(".");
    },
  },

  computed: {
    ...mapState(["hoteldata", "moteldata", "villadata"]),
  },
  created() {
    if (this.id == 0) {
      this.data = this.moteldata[this.number];
    } else if (this.id == 1) {
      this.data = this.hoteldata[this.number];
    } else if (this.id == 2) {
      this.showVilla = true;
      this.data = this.villadata[this.number];
    }
  },
};
</script>

