<template>
  <div class="content">
    <div class="row">
      <FilterList
        @changeFilter="handleFilterChange"
        :filter_list="filter_list"
        :roomCondition="roomCondition"
      />
      <RoomListVue
        :id="id"
        :opensearch="opensearch"
        :searchRoomList="searchRoomList"
        :roomList="roomList"
      />
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import RoomListVue from "./RoomList.vue";
import FilterList from "./FilterList.vue";
export default {
  props: ["id"],
  data() {
    return {
      showHotel: false,
      showVilla: false,
      opensearch: false,
      roomList: [],
      roomCondition: [],
      searchRoomList: [],
      filter_list: [],
    };
  },
  components: {
    RoomListVue,
    FilterList,
  },
  computed: {
    ...mapState([
      "hoteldata",
      "hotelConditions",
      "villadata",
      "villaCondition",
    ]),
  },
  methods: {
    handleFilterChange(dataFromChild) {
      // 자식 컴포넌트에서 받은 데이터(dataFromChild)를 처리
      if (this.searchRoomList.length > 0) {
        this.searchRoomList = [];
        this.pushdata(dataFromChild);
      } else {
        this.pushdata(dataFromChild);
      }
    },
    pushdata(dataFromChild) {
      this.filter_list = dataFromChild;
      for (let index = this.roomList.length - 1; index >= 0; index--) {
        const arr = this.roomList[index].Services.filter((x) =>
          this.filter_list.includes(x)
        );
        if (arr.length !== 0 || this.filter_list[1] == this.roomList[index].예약가능날짜 || this.filter_list[2] == this.roomList[index].category) {
          // arr이 빈 배열이 아닐 경우 searchroomList에 추가
          this.searchRoomList.push(this.roomList[index]);
        }
        // if (this.filter_list.length <= 2) {
        //   this.refreshAll();
        // }
        this.searchRoomList.reverse();
      }
      
      this.opensearch = true;
    },
    refreshAll() {
      this.$router.go();
    },
  },

  created() {
    if (this.id == 1) {
      this.showHotel = true;
      this.roomList = this.hoteldata;
      this.roomCondition = this.hotelConditions;
    } else if (this.id == 2) {
      this.showVilla = true;
      this.roomList = this.villadata;
      this.roomCondition = this.villaCondition;
    }
  },
};
</script>

