<script>
export default {
  props :['scrollY'],
  data(){
    return{
      background : 'defaultbackround'
    }
  },
  mounted() {
    this.$watch('scrollY', () => {
      if (this.scrollY <= 0) {
        this.background = 'defaultbackround'
      } else {
        this.background = 'scrollBackground'
      }
    });
  },
};
