<template>
  <div class="col p-4 border filter-wrap">
    <div class="d-flex flex-row fs-5 fw-bold mb-3">날짜</div>
    <div class="d-flex flex-row mb-4">
      <input @change="changeDate();" v-model="selectDate" type="date" name="" id="" />
    </div>
    <div class="detailedConditions">
      <div class="text-start fs-5 fw-bold mb-3">상세조건</div>
      <div class="row mb-4">
        <div style="padding-left: 0px; padding-right: auto" class="col">
          <button
            @click="refreshAll()"
            class="btn btn-outline-danger"
            style="width: 100%"
          >
            초기화
          </button>
        </div>
        <div style="padding-left: 0px; padding-right: 0px" class="col">
          <button
            @click="$emit('changeFilter',data)"
            style="width: 100%"
            class="btn btn-danger"
          >
            적용
          </button>
        </div>
      </div>

      <div class="mb-4 text-start">
        <div class="pb-2 pt-2">
          <input
            type="checkbox"
            class="form-check-input me-2"
            :id="isReservedResult"
            v-model="data"
            :value="`${isReservedResult}`"
          />
          <label class="form-check-label cs-font" :for="isReservedResult">
            {{ isReservedResult }}</label
          >
        </div>
      </div>

        
        <div class="d-flex flex-column mb-4">
          <div class="me-auto mb-3 fs-5 fw-bold">유형</div>
        <div
          v-for="(list, index) in roomCondition.type"
          :key="index"
        >
          <div class="form-check">
            <input
              type="checkbox"
              class="form-check-input "
              :id="list"
              v-model="data"
              :value="`${list}`"
            />
            <label class="form-check-label cs-font text-start" :for="list">{{
              list
            }}</label>
          </div>
        </div>
      </div>


      <div class="d-flex flex-row mb-4">
        <div class="me-auto fs-5 fw-bold">인원</div>

        <span @click="decrement" class="me-auto mouseon">-</span>
        <span class="me-auto">{{ count }}</span>
        <span @click="increment" class="me-0 mouseon">+</span>
      </div>

      <div class="text-start mb-3 fs-5 fw-bold">공용시설</div>

      <div class="row mb-4">
        <div
          v-for="(list, index) in roomCondition.publicFacilities"
          :key="index"
          class="col-md-6"
        >
          <div class="form-check">
            <input
              type="checkbox"
              class="form-check-input"
              :id="list"
              v-model="data"
              :value="`${list}`"
            />
            <label class="form-check-label cs-font" :for="list">{{
              list
            }}</label>
          </div>
        </div>
      </div>

      <div class="text-start mb-3 fs-5 fw-bold">객실 내 시설</div>
      <div class="row mb-4">
        <div
          v-for="(list, index) in roomCondition.roomAmenities"
          :key="index"
          class="col-md-6"
        >
          <div class="form-check">
            <input
              type="checkbox"
              class="form-check-input"
              :id="list"
              v-model="data"
              :value="`${list}`"
            />
            <label class="form-check-label cs-font" :for="list">{{
              list
            }}</label>
          </div>
        </div>
      </div>
    </div>

    <div class="text-start mb-3 fs-5 fw-bold">기타</div>
    <div class="row mb-4">
      <div
        v-for="(list, index) in roomCondition.others"
        :key="index"
        class="col-md-6"
      >
        <div class="form-check">
          <input
            type="checkbox"
            class="form-check-input"
            :id="list"
            v-model="data"
            :value="`${list}`"
          />
          <label class="form-check-label cs-font" :for="list">{{ list }}</label>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    roomCondition: Array,
    filter_list : Array
  },
  data() {
    return {
      data: this.filter_list,
      count: 2,
      isReservedResult: "예약가능",
      selectDate: "",
    };
  },
  
  methods: {
    changeDate() {
      this.data[1] = this.selectDate
    },
    refreshAll() {
      this.$router.go();
    },

    increment() {
      this.count += 1;
      this.data[0] = this.count;
    },
    decrement() {
      if (this.count > 0) {
        this.count -= 1;
        this.data[0] = this.count;
      }
    },
  },
  created() {
    this.data[0] = this.count;
    this.data[1] = this.selectDate
  },
};
</script>

<style>
.mouseon{
  cursor: pointer;

}
</style>