import { createWebHistory, createRouter } from "vue-router";
import Home from "./components/HomePage.vue";
import Motel from "./components/Motel/MotelPage.vue";
import Detail from "./components/DetailPage.vue"
import YeyakList from "./components/YeyakList.vue";
const routes = [
  {
    path: "/badchoicepo",
    component: Home,
  },

  {
    path: "/badchoicepo/product/0",
    component: Motel,
  },
  {
    path : "/badchoicepo/product/detail/:id/:number",
    name : "Detail",
    component : Detail,
    props: true, // props를 사용하도록 설정
  },
  { path: "/badchoicepo/product/:id(\\d+)", component: YeyakList , props : true },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;
