<template>
  <div class="col">
    <div v-if="opensearch">
      {{ changetext() }}
      <div v-if="searchRoomList.length <= 0">
        <div class="item-list pb-2 d-flex justify-content-between">
          <div class="fs-5">
            죄송합니다. 해당 필터에 맞는 방을 찾지 못하였습니다.
          </div>
        </div>
      </div>
      <div class="gomargin" v-else>
        <div class="item-list pb-2 d-flex justify-content-between">
          <div class="fs-4">필터검색 결과 ({{ searchRoomList.length }})</div>
        </div>

        <div
          v-for="(data, index) in searchRoomList"
          :key="index"
          class="item-list d-flex flex-column"
         
        >
        <router-link :to="`/badchoicepo/product/detail/${id}/${searchRoomList[index].id}`">

            <div class="item border">
              <img
                style="background-color: rgba(0, 0, 0, 0.5)"
                class="image-thumbnail"
                :src="data.thumbnailImage"
              />
              <div class="dark-overlay"></div>
              <div class="evt-info">
                <span
                  class="pt-2 text-white"
                  style="{
                  'margin-left'
                  'margin-right':
                }"
                >
                  킹왕짱
                </span>
              </div>
              <div class="text-start name text-white">
                <p class="fs-4 fw-bold">
                  {{ data.name }}
                </p>
                <p class="fs-4">
                  {{ data.address }}
                </p>
              </div>
              <div class="price text-end fs-5 text-white">
                <p class="fw-bold">{{ formatNumber(data.price[0]) }}원</p>
              </div>
            </div>
          </router-link>
        </div>
      </div>
    </div>

    <div class="item-list mt-3 pb-2 d-flex justify-content-between">
      <div class="fs-5">{{ text }}</div>
      <div class="fs-6">광고</div>
    </div>
    <div
      v-for="(data, index) in roomList"
      :key="index"
      class="item-list d-flex flex-column"
    >
    <router-link :to="`/badchoicepo/product/detail/${id}/${index}`">
      <div class="item border">
        <img
          style="background-color: rgba(0, 0, 0, 0.5)"
          class="image-thumbnail"
          :src="data.thumbnailImage"
        />
        <div class="dark-overlay"></div>
        <!-- 어두운 효과를 주는 요소 -->
        <div class="evt-info">
          <span
            class="pt-2 text-white"
            style="{
                  'margin-left'
                  'margin-right':
                }"
          >
            킹왕짱
          </span>
        </div>
        <div class="text-start name text-white">
          <p class="fs-4 fw-bold">
            {{ data.name }}
          </p>
          <p class="fs-4">
            {{ data.address }}
          </p>
        </div>
        <div class="price text-end fs-5 text-white">
          <p class="fw-bold">{{ formatNumber(data.price[0]) }}원</p>
        </div>
      </div>
    </router-link>
    </div>
    
  </div>
</template>

<script>
export default {
  props: {
    roomList: Array,
    searchRoomList: Array,
    opensearch: Boolean,
    id : Number,
    number : Number
  },
  data() {
    return {
      text: "인기추천",
    };
  },
  methods: {
    formatNumber(number) {
      // 숫자를 문자열로 변환
      const numberString = number.toString();

      // 숫자를 ','를 기준으로 분할
      const parts = numberString.split(".");

      // 정수 부분에 ',' 추가
      parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",");

      // 다시 합치기
      return parts.join(".");
    },
    changetext() {
      if (this.opensearch) this.text = "그러면 여긴 어떠신가요?";
    },
  },
  computed() {
    this.changetext();
  },
};
</script>

<style>
.gomargin{
  margin-bottom: 100px;
}
</style>
